export default class Store implements NewsStore {
    private feeds:NewsFeed[];
    private _currentPage: number;

    constructor(){
        this.feeds = [];
        this._currentPage = 1;
    }
    // í´ë˜ìŠ¤ ì•ˆ ë³€ìˆ˜ëª…ê³¼ ë™ì¼í•˜ë©´ ì•ˆë¨. 
    // ê·¸ë˜ì„œ ì£¼ë¡œ ë‚´ë¶€ì—ì„œ ì“°ëŠ” ë³€ìˆ˜ëª… ì•ì— _ í‘œì‹œ
    get currentPage(){
        return this._currentPage;
    }

    set currentPage(page: number){
        this._currentPage = page;
    }

    get nextPage(): number {
        return this._currentPage +1;
    }

    get prevPage(): number {
        return this._currentPage > 1 ? this._currentPage-1: 1;
    }

    get numberOfFeed(): number {
        return this.feeds.length;
    }

    get hasFeeds(): boolean {
        return this.feeds.length > 0;
    }

    getAllFeeds(): NewsFeed[] {
      return this.feeds;  
    }

    getFeed(position: number): NewsFeed {
        return this.feeds[position];
    }

    // ìŠ¤í”„ë ˆë“œ ì˜¤í¼ë ˆì´í„°
    setFeeds(feeds: NewsFeed[]) : void {
        console.log("feeds", feeds);
        this.feeds = feeds.map(feed => ({
            ...feed,
            read:false,
        }))
    }

    makeRead(id: number): void {
        const feed = this.feeds.find((feed: NewsFeed) => feed.id === id);
        if (feed) {
            feed.read = true;
        }
    }
}